/*--------------------------------------------------------------
# Header
--------------------------------------------------------------*/

@import url('https://fonts.googleapis.com/css2?family=Poppins:wght@400;500;600;700&family=Quicksand:wght@300;400;500;600;700&display=swap');
* {
    margin: 0;
    padding: 0;
    font-family: 'Poppins', sans-serif;
    box-sizing: border-box;
}

.app-bar {
    display: flex;
    position: relative;
    justify-content: space-between;
    align-items: center;
    background-color: rgb(229, 229, 229);
}

.app-bar__navigation {
    height: 100%;
}

.app-bar .app-bar__brand img {
    height: 100px;
    width: auto;
}

.app-bar .app-bar__navigation ul {
    display: flex;
    margin-right: 30px;
    padding: 0;
}

.app-bar .app-bar__navigation ul li {
    list-style: none;
}

.app-bar .app-bar__navigation ul li a {
    display: block;
    text-decoration: none;
    padding: 1rem;
    color: #D0AC8A;
}

.app-bar__navigation ul li a:hover {
    color: #FF0B66;
    transition: all 0.3s ease-in-out;
}

.app-bar__navigation ul li button {
    width: 44px;
    height: 44px;
    margin-left: 10px;
    margin-top: 10px;
    color: rgb(255, 255, 255);
    border-radius: 50%;
    border: 2px;
    background-color: rgb(203, 0, 0);
}

.app-bar__navigation ul li button:hover {
    color: black;
    transition: all 0.3s ease-in-out;
}

.jumbo {
    background-image: url('../img/babycaree.jpg');
    width: 100%;
    top: 0;
    min-height: 500px;
    background-size: cover;
    background-position: center;
    display: flex;
    text-align: center;
    align-items: center;
    justify-content: center;
}

.jumbotron_ {
    color: #FF0B66;
    font-size: 50px;
    font-weight: 500;
}

.jumbotron__ p {
    color: #FF0B66;
}

.Headline {
    display: grid;
    grid-template-columns: 1fr 1fr;
    font-size: 40px;
    padding: 20px;
    color: #FF0B66;
    margin: 0;
}

.Headline_kiri {
    margin-top: 150px;
    font-size: 20px;
    padding: 20px;
    color: #FF0B66;
}

.Headline_kanan {
    width: 100%;
    margin-left: 20px;
}

.title_page_article {
    text-align: center;
    font-size: 18px;
    padding: 10px;
    margin-top: 40px;
    margin-bottom: 50px;
    font-family: sans-serif;
    color: rgb(33, 31, 31);
}

.title_page_article p {
    font-size: 16px;
    margin-top: 5px;
}

footer {
    background-color: #FF0B66;
    padding: 12px;
    color: white;
    text-align: center;
}

.article-list {
    display: grid;
    width: fit-content;
    grid-template-columns: 1fr 1fr 1fr;
    margin: 20px 100px;
}

.article-item {
    margin: 0 10px 18px 10px;
    box-shadow: 0 4px 8px 0 rgba(113, 107, 107, 0.2);
    border-radius: 10px;
    overflow: hidden;
    max-width: 400px;
    width: 100%;
}

.content {
    width: 100%;
    padding: 5px;
}

.article-info {
    padding: 15px;
}

.title-article {
    font-size: 22px;
    padding: 5px;
    margin-bottom: 10px;
}

.desc-article {
    text-align: justify;
    font-size: 15px;
    font-family: 'Poppins', sans-serif;
    padding: 5px;
}